import React, { useEffect, useState } from 'react'
import '../StyleCSS/Slide.css'

function SlideShow({ data }) {
    const [initSlide, setSlides] = useState([]);

    //khi d√πng state ki·ªÉu n√†y th√¨ s·∫Ω r·∫•t quan tr·ªçng key
    //react s·∫Ω d·ª±a v√†o key v√† t·∫°o ra m·ªôt DOM m·ªõi v√† so s√°nh v·ªõi DOM C≈© n·∫øu nh·∫≠n th·∫•y key kh√¥ng thay ƒë·ªïi th√¨ react s·∫Ω kh√¥ng render l·∫°i to√†n b·ªô
    // trong tr∆∞·ªùng h·ª£p slide n√†y th√¨ khi item-slide c√≥ key l√† 1 b·ªã d·ªùi v·ªÅ cu·ªëi array, n√≥ s·∫Ω t∆∞∆°ng t·ª± nh∆∞ appenchild

    useEffect(() => {
        if (data) {
            setSlides(data);
        }
    }, [data])


    const handleNext = () => {
        setSlides(prev => [...prev.slice(1), prev[0]]);
    };

    const handlePrev = () => {
        setSlides(prev => [prev[prev.length - 1], ...prev.slice(0, prev.length - 1)]);
    };

    return (
        <div className='container-Slide p-4'>
            <div className='slide'>
                { initSlide.map((item) => {
                    return (
                        <div className='item-slide' style={ { backgroundImage: `url(${process.env.PUBLIC_URL}${item.ImageUrl})` } } key={ item.id }>
                            <div className='content-slide'>
                                <div className='name-slide'>{ item.Title }</div>
                                <div className='des line-clamp'>{ item.Description }</div>
                                <button className='border-0 rounded-2'>See more</button>
                            </div>
                        </div>
                    )
                }) }
            </div>
            <div className='button-slide'>
                <button className='prev' onClick={ handlePrev }><i className="fa-solid fa-arrow-left"></i></button>
                <button className='next' onClick={ handleNext }><i className="fa-solid fa-arrow-right"></i></button>
            </div>
        </div>
    )
}

export default SlideShow